import _fetch from '@/utils/axios'

// 获取用户频道列表——横
function getChannel () {
  return _fetch({
    url: '/v1_0/user/channels',
    method: 'get',
    headers: {
      contentType: 'application/json'
    }
  })
}
// 获取文章列表——竖
function getArticleList (data) {
  return _fetch({
    url: '/v1_1/articles',
    method: 'get',
    headers: {
      contentType: 'application/json'
    },
    params: data
  })
}

// 获取搜索内容
function getSearchApi (data) {
  return _fetch({
    url: '/v1_0/search',
    method: 'GET',
    headers: {
      contentType: 'application/json'
    },
    params: data
  })
}
// 删除搜索历史
function removeApi () {
  return _fetch({
    url: '/v1_0/search/histories',
    method: 'DELETE',
    headers: {
      contentType: 'application/x-www-form-urlencoded'
    }
  })
}

// 获取用户频道列表——横
function getAllChannel () {
  return _fetch({
    url: '/v1_0/channels',
    method: 'get',
    headers: {
      contentType: 'application/json'
    }
  })
}

function deleteCannel (id) {
  console.log(id)
  return _fetch({
    url: '/v1_0/user/channels/' + id,
    method: 'DELETE',
    headers: {
      contentType: 'application/x-www-form-urlencoded'
    }
  })
}

function addCannel (obj) {
  return _fetch({
    url: '/v1_0/user/channels/' + obj.id,
    method: 'PUT',
    headers: {
      contentType: 'application/json'
    },
    data: {
      seq: obj.seq
    }
  })
}

// 获取热门搜索历史记录
function hotSearchApi () {
  return _fetch({
    url: '/v1_0/search/histories',
    method: 'GET',
    headers: {
      contentType: 'application/json'
    }
  })
}
// 获取联想数据
function associationApi (data) {
  return _fetch({
    url: '/v1_0/suggestion',
    method: 'GET',
    headers: {
      contentType: 'application/json'
    },
    params: data
  })
}

export { getChannel, getArticleList, getAllChannel, deleteCannel, addCannel, hotSearchApi, removeApi, associationApi, getSearchApi }
