<template>
  <div class="font-mono">
    <div class="nav">
      <tab title="搜索结果" @left="onClickLeft" :class="[{'animate__animated animate__fadeIn': controlAnimate}]" />
    </div>
    <div class="px-3 font-bold text-coolGray-500 text-2xl pt-2">{{ value || $route.query.requies }}</div>
    <!-- 中间内容 -->
    <div v-for="(item, index) in searchList" :key="index" class="mt-1 border-b-2 w-11/12 mx-auto font-mono shadow-md ">
      <div class="w-full px-3 text-sm">
        <div class="w-full">
          <h4 class="font-bold text-lg pb-2 pt-4 px-1 leading-4">
            {{ searchList[index].title }}
          </h4>
          <!---->
          <div class="w-full text-xs text-gray-400">
            <div class="pl-6">
              <span>{{ searchList[index].aut_name }}</span>
              <span  class="px-20">{{ searchList[index].pubdate |dayfilter }}</span>
              <span>{{ searchList[index].ch_id }}评论</span>
            </div>
          </div>
        </div>
      </div>
      <div class="flex items-center py-4">
        <div class="flex-1 text-lg">
          <div class="flex justify-center items-center " >
            <i class="van-icon van-icon-comment-o van-grid-item__icon pr-2">
            </i ><span class="text-base">0</span>
          </div>
        </div>
        <div
          class="flex-1 text-lg"
        >
          <div
            class="flex justify-center items-center "
          >
            <i class="van-icon van-icon-like-o van-grid-item__icon pr-2"><!----></i
            ><span class="text-base">点赞</span>
          </div>
        </div>
        <div
          class="flex-1 text-lg"
        >
          <div
            class="flex justify-center items-center "
          >
            <i class="van-icon van-icon-exchange van-grid-item__icon pr-2"
            ><!----></i
            ><span class="text-base">分享</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getSearchApi } from '../../../api/indexApi.js'
import Tab from '@/components/Tab'

export default {
  components: { Tab },
  data () {
    return {
      value: '',
      searchList: [],
      controlAnimate: true
    }
  },
  // 获取搜索结果
  async created () {
    const res = await getSearchApi({
      q: this.value || this.$route.query.requies
    })
    console.log(res)
    this.searchList = res.data.results
  },
  methods: {
    // 返回搜索页面
    onClickLeft () {
      this.$router.push('/search')
    }
  }
}
</script>
